const headerCenter = document.querySelector('.header__center');
const headerBottom = document.querySelector('.header__bottom');

const headerBtnReg = document.querySelector('.header__btn-reg');
const mainNavList = document.querySelector('.main-nav__list');

const initMoveHeaderElements = () => {
  const windowWidth = window.innerWidth;

 
  if (windowWidth <= 768) {
    if (headerBtnReg && headerCenter && mainNavList && !headerCenter.contains(headerBtnReg)) {
      mainNavList.insertAdjacentElement('afterend', headerBtnReg);
    }
  } else {
    if (headerBtnReg && headerBottom && !headerBottom.contains(headerBtnReg)) {
      headerBottom.appendChild(headerBtnReg);
    }
  }
};

initMoveHeaderElements();
window.addEventListener('resize', initMoveHeaderElements);

export { initMoveHeaderElements };
