document.addEventListener('DOMContentLoaded', () => {
  const input = document.querySelector('input[name="code"]');
  const submitBtn = document.getElementById('code-submit');
  const MAX_LENGTH = 6;

  input.addEventListener('input', () => {
    // Убираем всё, кроме латинских букв и цифр
    let value = input.value.toUpperCase().replace(/[^A-Z0-9]/g, '');
    // Ограничиваем до 6 символов
    if (value.length > MAX_LENGTH) {
      value = value.slice(0, MAX_LENGTH);
    }
    input.value = value;
    // Активируем кнопку, если всё заполнено
    submitBtn.disabled = value.length !== MAX_LENGTH;
  });

  document.getElementById('promo-form').addEventListener('submit', (e) => {
    e.preventDefault();
    const code = input.value.trim();
    if (code.length === MAX_LENGTH) {
      alert('Код зарегистрирован: ' + code);
      input.value = '';
      submitBtn.disabled = true;
    }
  });
});