const sliders = document.querySelectorAll('.wash__slider');

const initBannerSlider = () => {
  sliders.forEach((slider) => {
    let swiper;

    const enableSwiper = () => {
      swiper = new Swiper(slider, {
        slidesPerView: 'auto',
        spaceBetween: 20,
        speed: 800,
        breakpoints: {
          300: {
            spaceBetween: 15,
          },
          520: {
            spaceBetween: 20,
          },
        },
        navigation: {
          nextEl: '.swiper-button-next.wash-next',
          prevEl: '.swiper-button-prev.wash-prev',
        },
      });
    };

    const handleMode = () => {
      const isTwo = slider.classList.contains('wash__slider--two');
      const isMobile = window.innerWidth <= 520;

      if (isTwo && isMobile) {
        
        if (swiper) {
          swiper.destroy(true, true);
          swiper = null;
        }
        return;
      }

      
      if (!swiper) {
        enableSwiper();
      }
    };

    handleMode();
    window.addEventListener('resize', handleMode);
  });
};

export { initBannerSlider };
