const slider = document.querySelector('[data-slider="chance"]');

const chanceSlider = () => {
    if (!slider) {
      return;
    }
  
    const swiper = new Swiper(slider, {
        effect: 'coverflow',
        grabCursor: true,
        centeredSlides: true,     
        slidesPerView: 'auto',           
        initialSlide: 1,
        
        coverflowEffect: {
          rotate: 0,              
          stretch: 0,            
          depth: 250,             
          modifier: 1,            
          slideShadows: false,
          scale: 0.8,              
        },
        
        touchReleaseOnEdges: true, 
        resistance: true,         
        resistanceRatio: 0.5,  

        
        watchSlidesProgress: false, 
        updateOnWindowResize: true, 
        
        preloadImages: true,
        observer: true,
        observeParents: true,
        lazy: true,
        // loop: true,               
        speed: 800,             
        breakpoints: {
            
            300: {
              effect: 'slide', 
              slidesPerView: 1,
              spaceBetween: 0,
              initialSlide: 0,
            },

            620: {
              effect: 'coverflow',
              coverflowEffect: {
                rotate: 0,              
                stretch: 0,            
                depth: 300,             
                modifier: 1,            
                slideShadows: false,
                scale: 0.8,              
              },
              spaceBetween: 30,
              initialSlide: 1,
            },
            
            950: {
              coverflowEffect: {
                rotate: 0,              
                stretch: 0,            
                depth: 250,             
                modifier: 1,            
                slideShadows: false,
                scale: 0.8,              
              },
              spaceBetween: 50,
            },
        },
        
        navigation: {
            nextEl: '.swiper-button-next.chance-next',
            prevEl: '.swiper-button-prev.chance-prev',
        }
    });
};

export {chanceSlider};