export class Accordion {
  constructor() {
    this._documentClickHandler = this._documentClickHandler.bind(this);
    this._windowResizeHandler = this._windowResizeHandler.bind(this);
  }

  init() {
    document.addEventListener('click', this._documentClickHandler);
    window.addEventListener('resize', this._windowResizeHandler);
  }

  _documentClickHandler(evt) {
    const button = evt.target.closest('[data-accordion="button"]');
    if (!button) return;

    evt.preventDefault();

    const element = button.closest('[data-accordion="element"]');

    // Переключаем состояние (несколько можно открывать)
    if (element.classList.contains('is-active')) {
      this.closeAccordion(element);
    } else {
      this.openAccordion(element);
    }
  }

  openAccordion(element) {
    const content = element.querySelector('[data-accordion="content"]');
    const icon = element.querySelector('.accordion__icon');

    element.classList.add('is-active');

    // Анимация контента
    content.style.maxHeight = content.scrollHeight + 'px';
    content.style.opacity = '1';
    content.style.transition = 'max-height 0.4s ease, opacity 0.4s ease';

    // Анимация иконки (дополнительно к CSS, если нужно)
    if (icon) {
      const vertical = icon.querySelector('.line-vertical');
      if (vertical) {
        vertical.style.opacity = '0';
        vertical.style.transform = 'scaleY(0)';
      }
    }
  }

  closeAccordion(element) {
    const content = element.querySelector('[data-accordion="content"]');
    const icon = element.querySelector('.accordion__icon');

    element.classList.remove('is-active');

    // Плавное закрытие
    content.style.maxHeight = content.scrollHeight + 'px';
    requestAnimationFrame(() => {
      content.style.maxHeight = '0';
      content.style.opacity = '0';
    });

    // Возвращаем иконку в состояние "плюс"
    if (icon) {
      const vertical = icon.querySelector('.line-vertical');
      if (vertical) {
        vertical.style.opacity = '1';
        vertical.style.transform = 'scaleY(1)';
      }
    }
  }

  _windowResizeHandler() {
    // Пересчёт высоты для открытых элементов
    document.querySelectorAll('[data-accordion="element"].is-active').forEach((element) => {
      const content = element.querySelector('[data-accordion="content"]');
      content.style.maxHeight = content.scrollHeight + 'px';
    });
  }
}