import { ScrollLock } from '../../utils/scroll-lock';
import { FocusLock } from '../../utils/focus-lock';

export class Modals {
  constructor(settings = {}) {
    this._scrollLock = new ScrollLock();
    this._focusLock = new FocusLock();

    this._modalOpenElements = document.querySelectorAll('[data-open-modal]');
    this._stackModalElements = [];
    this._openedModalElement = null;
    this._modalName = null;
    this._enableScrolling = true;
    this._settingKey = 'default';
    this._settings = settings;

    // Настройки по умолчанию
    this._preventDefault = true;
    this._stopPlay = true;
    this._lockFocus = true;
    this._startFocus = true;
    this._focusBack = true;
    this._eventTimeout = 0;
    this._resetScrollPos = true;
    this._openCallback = null;
    this._closeCallback = null;

    // Бинды
    this._documentKeydownHandler = this._documentKeydownHandler.bind(this);
    this._documentClickHandler = this._documentClickHandler.bind(this);
    this._modalClickHandler = this._modalClickHandler.bind(this);

    this._init();
    this._initCookieModal(); // автооткрытие cookie
  }

  _init() {
    if (this._modalOpenElements.length) {
      document.addEventListener('click', this._documentClickHandler);
    }
  }

  _setSettings(settingKey = this._settingKey) {
    const settings = this._settings[settingKey];
    if (!settings) return;

    this._preventDefault = settings.preventDefault ?? this._preventDefault;
    this._stopPlay = settings.stopPlay ?? this._stopPlay;
    this._lockFocus = settings.lockFocus ?? this._lockFocus;
    this._startFocus = settings.startFocus ?? this._startFocus;
    this._focusBack = settings.focusBack ?? this._focusBack;
    this._resetScrollPos = settings.resetScrollPos ?? this._resetScrollPos;
    this._eventTimeout = settings.eventTimeout ?? this._eventTimeout;
    this._openCallback = settings.openCallback ?? this._openCallback;
    this._closeCallback = settings.closeCallback ?? this._closeCallback;
  }

  _documentClickHandler(evt) {
    const target = evt.target.closest('[data-open-modal]');
    if (!target) return;

    if (this._preventDefault) evt.preventDefault();
    const modalName = target.dataset.openModal;
    if (!modalName) return;

    this.open(modalName);
  }

  _documentKeydownHandler(evt) {
    if (evt.key === 'Escape' || evt.key === 'Esc') {
      evt.preventDefault();
      const active = document.querySelector('.modal.is-active');
      if (active) this.close(active.dataset.modal);
    }
  }

  _modalClickHandler(evt) {
    const target = evt.target.closest('[data-close-modal]');
    if (!target) return;

    const modalEl = target.closest('[data-modal]');
    if (!modalEl) return;

    if (target.dataset.closeModal === 'back') {
      this.back();
    } else {
      this.close(modalEl.dataset.modal);
      this._stackModalElements = [];
    }
  }

  _addListeners(modal) {
    modal.addEventListener('click', this._modalClickHandler);
    document.addEventListener('keydown', this._documentKeydownHandler);
  }

  _removeListeners(modal) {
    modal.removeEventListener('click', this._modalClickHandler);
    document.removeEventListener('keydown', this._documentKeydownHandler);
  }

  open(modalName) {
    const modal = document.querySelector(`[data-modal="${modalName}"]`);
    if (!modal || modal.classList.contains('is-active')) return;

    // Не трогаем обработчик кликов, чтобы другие кнопки продолжали работать
    this._openedModalElement = document.querySelector('.modal.is-active');

    // Если открыта другая модалка, закрываем её (кроме cookie)
    if (this._openedModalElement && modalName !== 'cookie') {
      this._enableScrolling = false;
      this.close(this._openedModalElement.dataset.modal);
    }

    this._setSettings(modalName);
    modal.classList.add('is-active');

    // Куки отображаются иначе — не блокируют прокрутку
    if (modalName === 'cookie') {
      setTimeout(() => modal.classList.add('is-visible'), 50);
    } else if (!this._openedModalElement) {
      this._scrollLock.disableScrolling();
    }

    // Добавляем в стек, чтобы работала функция back()
    if (modalName !== this._stackModalElements[this._stackModalElements.length - 1]) {
      this._stackModalElements.push(modalName);
    }

    if (this._openCallback) this._openCallback();
    if (this._lockFocus && modalName !== 'cookie') {
      this._focusLock.lock('.modal.is-active', this._startFocus);
    }

    if (this._resetScrollPos) modal.scrollTo(0, 0);

    // Автозакрытие для модалки "thanks" "congrat"
    if (modalName === 'thanks' || modalName === 'congrat') {
      setTimeout(() => this.close(modalName), 4000);
    }

    setTimeout(() => this._addListeners(modal), this._eventTimeout);
  }

  back() {
    if (!this._stackModalElements.length) return;

    const activeModal = this._stackModalElements.pop();
    const prevModal = this._stackModalElements[this._stackModalElements.length - 1];

    if (prevModal) {
      this.open(prevModal);
    }

    this.close(activeModal);
  }

  close(modalName) {
    const modal = document.querySelector(`[data-modal="${modalName}"]`);
    if (!modal || !modal.classList.contains('is-active')) return;

    if (this._lockFocus && modalName !== 'cookie') {
      this._focusLock.unlock(this._focusBack);
    }

    if (modalName === 'cookie') {
      localStorage.setItem('cookieModalClosed', 'true');
      modal.classList.remove('is-visible');
      setTimeout(() => modal.classList.remove('is-active'), 300);
    } else {
      modal.classList.remove('is-active');
      if (this._enableScrolling) {
        setTimeout(() => this._scrollLock.enableScrolling(), this._eventTimeout);
      }
    }

    this._removeListeners(modal);
    if (this._closeCallback) this._closeCallback();

    this._setSettings('default');
    this._enableScrolling = true;
  }

  _initCookieModal() {
    const cookieClosed = localStorage.getItem('cookieModalClosed');
    if (cookieClosed) return;

    // Открываем cookie через 2 секунды
    setTimeout(() => this.open('cookie'), 2000);
  }
}